<template>
    <v-layout
        row wrap
        justify-center
        align-center
        :style="{ 
            backgroundImage: 'url('+pepeGod+')',
            backgroundRepeat: 'no-repeat',
            backgroundSize: '100%',
        }"
        class="oldBody"
        pa-15
    >
        <v-flex xs11 lg5 pa-2 ma-1 class="formArea">
            <v-layout row wrap>
                <v-flex xs10 mx-3>
                    <v-text-field label="Nome" v-model="name"/>
                </v-flex>
                <v-flex xs10 mx-3>
                    <v-text-field label="Celular" v-model="phoneNumber" placeholder="XX XXXXX-XXXX"/>
                </v-flex>
                <v-flex xs10 mx-3>
                    <v-text-field label="Nmero Empresa" v-model="companyPhone"/>
                </v-flex>
                <v-flex xs10 mx-3>
                    <v-text-field label="Email" v-model="email"/>
                </v-flex>
                <v-flex xs10 mx-3>
                    <v-text-field label="Skype" v-model="communicationChannel"/>
                </v-flex>
            </v-layout>
        </v-flex>
        <v-flex xs12 lg5 pa-2 ma-1 class="formArea">
            <v-layout row wrap class="text-center">
                <v-flex 
                    id="outputImg" 
                    xs12
                    style="background-color: white; justify-content: center"
                    v-html="html85Based"/>
                <v-flex xs12 mt-16 mb-8 mx-3>
                    <v-btn
                        block
                        v-on:click="doCopyTable('outputImg')"
                    >
                        Copiar para Outlook
                    </v-btn>
                </v-flex>

                <v-flex xs12 my-8 mx-3> 
                    <v-btn 
                        block
                        v-on:click="doCopyText()"
                    >
                        Copiar para ThunderBird
                    </v-btn>
                </v-flex>
            </v-layout>
        </v-flex>
    </v-layout>
</template>

<script>
import pepeGod from "@/assets/projectsImg/pepe-god.jpg"


export default {
  name: "MailSignature",

  data() {
    return {
        name: "Batatoncio",
        phoneNumber: "+55 81",
        companyPhone: "+55 11 4002-8922",
        email: "lil.guyextreme@companymail.com.br",
        communicationChannel: "lil.guyextreme",
        companyPage: "https://wildclown.github.io/portifolio/",
        outOfficeAdvise: false,
        holidayAdvise: false,
        vacationAdvise: false,
        pepeGod: pepeGod
    };
  },
  computed: {
    html264Based: function() {
        let image264 = '<img src="data:image/png;base64," style="width: 85px;"/>';
        return this.makeHtmlString(image264, true)
    },
    html85Based: function() {
        let image85 = "<img src=\"data:image/png;base64,\" style=\"padding-right: 5px;\">";
        return this.makeHtmlString(image85)
    }
  },
  methods: {
    makeHtmlString(base64StringImg, isOutlook = false) {
        return (
            "<table id=\"assinaturaMV\" style=\"border: 1px solid black" +
            (isOutlook? ";margin: 0 auto; text-align:center\">" : "\">") +
            "<th style=\"padding-right: 15px;border-right: 2px solid #595959;\">"+
            base64StringImg +
            "</th>"+
            "<th style=\"text-align: left;padding-left: 15px;width: 300px;\">"+
            "<div style=\"font-size:12.0pt;font-family:Bahnschrift;color:black;\">"+
            "<b>" + this.name + "</b>"+
            "</div>"+
            "<div style=\"font-size:11pt;font-family:Bahnschrift;color:black;display: inline-block;font-weight: normal;\">"+
            (this.phoneNumber   == "+55 81" ? "" : "Mobile: " + this.phoneNumber + "<br>") +
            "Corporate: " + this.companyPhone + "<br>" +
            (this.communicationChannel   == "" ? "":"Teams : " + this.communicationChannel + "<br>") +
            "<a href=\"mailto:"+ this.email + "\" style=\"color:#17365D\">" + this.email + "</a><br>"+
            "<a href=\"" + this.companyPage + "\" style=\"color:#17365D\">" + this.companyPage + "</a><br>" +
            "</div>"+
            "<div style=\"display: inline;\">"+
            "<a href=\"" + this.companyPage + "\" target=\"_blank\"></a><br>" +
            "</div>" +
            "</th>" +
            "</table>"
        )
    },
    doCopyText() {
        navigator.clipboard.writeText(this.html264Based)
    },
    doCopyTable(id) {
        var sel, range;
        var el = document.getElementById(id); //get element id
        if (window.getSelection && document.createRange) { //Browser compatibility
        sel = window.getSelection();
        if(sel.toString() == ''){ //no text selection
            window.setTimeout(function(){
                range = document.createRange(); //range object
                range.selectNodeContents(el); //sets Range
                sel.removeAllRanges(); //remove all ranges from selection
                sel.addRange(range);//add Range to a Selection.
            },1);
        }
        }else if (document.selection) { //older ie
            sel = document.selection.createRange();
            if(sel.text == ''){ //no text selection
                range = document.body.createTextRange();//Creates TextRange object
                range.moveToElementText(el);//sets Range
                range.select(); //make selection.
            }
        }
        setTimeout(() => { document.execCommand('copy'); }, 500);
        }
  }
};
</script>
<style scoped>
    #outputImg{
        background-color: white;
    }

    .formArea{
        background-color: #313131;
        box-shadow: 3px 3px 2px rgb(54, 54, 54);
    }
    
</style>